.gpt3__blog-container_article {
	width: 100%;
	height: 100%;

	display: flex;
	flex-direction: column;
	background: var(--color-footer);
	transition: transform 0.3s ease 0s;
}
.gpt3__blog-container_article:hover {
	transform: scale(1.03);
	cursor: pointer;
}

.gpt3__blog-container_article-image {
	width: 100%;
	height: 100%;
	background: var(--color-bg);
}

.gpt3__blog-container_article-image img {
	width: 100%;
	height: 100%;
}

.gpt3__blog-container_article-content {
	display: flex;
	flex-direction: column;
	justify-content: space-between;

	padding: 1rem 1.5rem;
	height: 100%;
}

.gpt3__blog-container_article-content p {
	font-family: var(--font-family);
	font-size: 11.65px;
	font-weight: 700;
	line-height: 35px;

	color: #fff;
}

.gpt3__blog-container_article-content h3 {
	font-family: var(--font-family);
	font-size: 25.11px;
	font-weight: 800;
	line-height: 30.3px;

	color: #fff;
	margin-bottom: 5rem;
	cursor: pointer;
}

.gpt3__blog-container_article-content p:last-child {
	cursor: pointer;
}

@media screen and (max-width: 550px) {
	.gpt3__blog-container_article-content h3 {
		font-size: 18px;
		line-height: 25p;
	}
}
